import React from 'react';
import './button.css';

export interface ButtonProps extends
  React.DetailedHTMLProps<React.ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement> {
  variant?: 'primary' | 'secondary' | 'danger'
}

export const Button: React.FC<ButtonProps> = ({ variant = 'primary', ...props }) => {
  const {children, style} = props;

  const _style: React.CSSProperties = style || {};
  var design_tokens = require('json! ../design_tokens/tokens.json');

  switch(variant) {
    case 'primary':
      _style.backgroundColor = "#194fdb";
      _style.color = '#fff';
      break;
    case 'secondary':
      _style.backgroundColor = "#F8CB2E";
      _style.color = '#000';
      break;
    case 'danger':
      _style.backgroundColor = "#B22727";
      _style.color = '#fff';
      break;
  }

  return (
    // <button style={_style} {...props}>{children}</button>
    // <button className={'primary-button'} {...props}>{children}</button>
    <button className={'primary-button'} {...props}>{children}</button>
  )
}